<template>
    <div>
        <el-card>
            <el-descriptions class="margin-top" title="作业信息" :column="2" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <user />
                            </el-icon>
                            题目
                        </div>
                    </template>
                    {{ work.title_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <iphone />
                            </el-icon>
                            出题老师
                        </div>
                    </template>
                    {{ work.teacher_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <location />
                            </el-icon>
                            课程
                        </div>
                    </template>
                    {{ work.course_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            状态
                        </div>
                    </template>
                    <el-tag type="danger" size="small" v-if="nowTime < work.start_time">
                        未开始</el-tag>
                    <el-tag type="success" size="small" v-if="nowTime > work.start_time && nowTime < work.end_time">
                        进行中</el-tag>
                    <el-tag type="warning" size="small" v-if="nowTime > work.end_time">
                        已结束</el-tag>
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            开始时间
                        </div>
                    </template>
                    {{ work.start_time }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            结束时间
                        </div>
                    </template>
                    {{ work.end_time }}
                </el-descriptions-item>
            </el-descriptions>
            <el-descriptions direction="vertical" :column="1" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            内容
                        </div>
                    </template>
                    <pre>{{ work.title_content }}</pre>
                </el-descriptions-item>
            </el-descriptions>
        </el-card>
        <el-card class="up" v-if="isUploadok">
            <el-form :model="operaForm" label-width="120px">
                <el-form-item label="上传方式">
                    <el-radio-group v-model="mode" size="large">
                        <el-radio-button label="上传文字" />
                        <el-radio-button label="上传文件" />
                        <el-radio-button label="上传图片" />
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="文字答案" v-if="mode == '上传文字'">
                    <el-input v-model="operaForm.content" type="textarea" />
                </el-form-item>

                <el-form-item label="上传文件" v-if="mode == '上传文件'">
                    <el-upload v-model:file-list="fileList" name="work_file"
                        action="http://127.0.0.1:11002/work/work/upload/file" :headers="headers" :limit=1 multiple
                        :on-success="uploadfileSuccess">
                        <el-button type="primary">上传作业文件</el-button>
                        <template #tip>
                            <div class="el-upload__tip">
                                jpg/png files with a size less than 500KB.
                            </div>
                        </template>
                    </el-upload>
                </el-form-item>

                <el-form-item label="上传图片" v-if="mode == '上传图片'">
                    <el-upload v-model:file-list="PicList" name="work_file"
                        action="http://127.0.0.1:11002/work/work/upload/file" :headers="headers" :limit=5
                        list-type="picture-card" :on-preview="handlePictureCardPreview" :on-success="uploadSuccess"
                        :on-remove="handleRemove">
                        <el-icon>
                            <Plus />
                        </el-icon>
                    </el-upload>

                    <el-dialog v-model="dialogVisible">
                        <img w-full :src="dialogImageUrl" alt="Preview Image" />
                    </el-dialog>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="onSubmit">提交作业</el-button>
                    <el-button type="primary" @click="notUpdate">返回</el-button>
                </el-form-item>
            </el-form>
        </el-card>
        <el-card class="up" v-if="!isUpload">
            <el-descriptions class="margin-top" title="回答信息" :column="2" :size="size" border>
                <template #extra>
                    <el-button type="primary" @click="updateWork()">修改答案</el-button>
                </template>


                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">  
                            <el-icon :style="iconStyle">
                                <user />
                            </el-icon>
                            题目
                        </div>
                    </template>
                    {{ work.title_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <iphone />
                            </el-icon>
                            上传方式
                        </div>
                    </template>
                    {{ answer.content_msg.work_type }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <location />
                            </el-icon>
                            首次上传时间
                        </div>
                    </template>
                    {{ answer.create_time }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            修改时间
                        </div>
                    </template>
                    {{ answer.update_time }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            作业查重情况
                        </div>
                    </template>
                    <el-tag size="small" v-if="answer.hamming_distance == -2">待查重</el-tag>
                    <el-tag type="success" size="small" v-if="answer.hamming_distance == -1">没有重复</el-tag>
                    <el-tag type="danger" size="small" v-if="answer.hamming_distance > -1 && answer.hamming_distance < 4">
                        海明距离：{{ answer.hamming_distance }}</el-tag>
                    <el-tag type="warning" size="small" v-if="answer.hamming_distance > 3">
                        {{ answer.hamming_distance }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            状态
                        </div>
                    </template>
                    <el-tag size="small" v-if="answer.check_state == 1">待查重</el-tag>
                    <el-tag type="success" size="small" v-if="answer.check_state == 2">已查重</el-tag>

                </el-descriptions-item>
            </el-descriptions>
            <el-descriptions direction="vertical" :column="1" :size="size" border v-if="isUploadFile">
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            文件
                        </div>
                    </template>
                    <el-icon :style="iconStyle">
                        <Document />
                    </el-icon>
                    {{ answer.content_msg.file_name }}
                    <div style="float: right">
                        <el-button type="primary" @click="downloadFile()">下载</el-button>
                    </div>
                </el-descriptions-item>
            </el-descriptions>

            <el-descriptions direction="vertical" :column="1" :size="size" border v-if="isUploadPic">
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            图片
                        </div>
                    </template>
                    <div class="demo-image__preview">
                        <el-image v-for="url in answer.content_msg.pic_url"
                            style="width: 100px; height: 100px; margin-left: 20px" :src="url" :preview-src-list="[url]"
                            :initial-index="4" fit="cover" />
                    </div>
                </el-descriptions-item>
            </el-descriptions>

            <!-- <el-descriptions direction="vertical" :column="1" :size="size" border v-if="!isUpload">
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            内容
                        </div>
                    </template>
                    <pre>{{ answer.content_msg.content }}</pre>
                </el-descriptions-item>
            </el-descriptions> -->

        </el-card>
    </div>
</template>
<script>
import moment from "moment";
import axios from "axios";

import { work_find, work_upload, work_answer_details } from "@/api/student/api.js";
// eslint-disable-next-line no-unused-vars
export default {
    data() {
        return {

            work: {}
            ,
            tableData: [],

            //是否完成上传 false 已经上传了 true 没有上传
            // 控制展示答案框 true 不显示 false 显示
            isUpload: true,

            //控制显示上传框  true 显示  false 不显示
            isUploadok: false,

            //是否是修改答案 控制返回按钮是否显示
            isUpdate: false,

            nowTime: moment().format('YYYY-MM-DD HH:mm:ss'),
            fileList: [],
            PicList: [],
            // 实际上传用

            mode: "上传文字",
            operaForm: {
                pic_url: [],
            },
            headers: {
                token: sessionStorage.getItem('token'),
                user_id: sessionStorage.getItem('user_id'),
            },
            answer: {
                hamming_distance: "",
                hash_64: "",
                create_time: "",
                update_time: "",
                content_msg: {
                    content: "",
                    file_name: "",
                    file_url: "",
                    pic_url: [],
                    work_type: 0
                }
            }


        }
    },
    created() {
        this.getUserMessage();
        this.getAnswerMessage();
    },
    methods: {
        async getUserMessage() {
            var work_id = this.$route.query.work_id
            work_find({ work_id: work_id }).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.work = res.data.data.data[0]

                    // this.$router.push('/manager').catch(() => { })
                }
                if (this.nowTime > this.work.start_time && this.nowTime < this.work.end_time && this.isUpload) {
                    this.isUploadok = true
                }

            })
        },
        async getAnswerMessage() {
            var work_id = this.$route.query.work_id
            console.log(work_id);
            work_answer_details({ work_id: work_id }).then(res => {
                console.log(res);
                if (res.data.code != 200) {
                    // this.$message.error(res.data.msg);
                } else {
                    var resan = res.data.data
                    if (resan.content_msg.work_type == 1) {
                        resan.content_msg.work_type = "上传文字"
                        this.isUploadFile = false
                        this.isUploadPic = false
                    }
                    if (resan.content_msg.work_type == 2) {
                        resan.content_msg.work_type = "上传文件"
                        this.isUploadFile = true
                        this.isUploadPic = false
                    }
                    if (resan.content_msg.work_type == 3) {
                        resan.content_msg.work_type = "上传图片"
                        this.isUploadFile = false
                        this.isUploadPic = true
                    }
                    this.answer = resan
                    this.isUpload = false
                    this.isUploadok = false
                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        uploadSuccess(file, fileList) {
            if (file.code != 200) {
                this.PicList.pop();
                this.$message({
                    showClose: true,
                    message: file.msg,
                    type: "error",
                })
            } else {
                var newfile = {
                    name: fileList.name,
                    url: fileList.response.data,
                }

                this.PicList.pop()
                this.PicList.push(newfile)
                this.$message({
                    showClose: true,
                    message: "上传成功",
                    type: "success",
                })
            }
        },
        uploadfileSuccess(file, fileList) {

            var newfile = {
                name: fileList.name,
                url: fileList.response.data,
            }
            this.fileList.pop()
            this.fileList.push(newfile)
            this.$message({
                showClose: true,
                message: "上传成功",
                type: "success",
            })
        },
        downloadFile() {
            var url = this.answer.content_msg.file_url
            var name = this.answer.content_msg.file_name
            axios.get(this.answer.content_msg.file_url)

            axios({
                method: 'get',
                url: url,
                responseType: 'blob',
            }).then(function (response) {
                const link = document.createElement('a')
                let blob = new Blob([response.data], { type: response.data.type })
                let url = URL.createObjectURL(blob)
                link.href = url
                link.download = name
                link.click()
            })

        },
        updateWork() {
            console.log(this.answer);

            if (this.answer.content_msg.file_url.length > 0) {
                var newfile = [{
                    name: this.answer.content_msg.file_name,
                    url: this.answer.content_msg.file_url,
                }]
                this.fileList = newfile
            }




            var piclist = []
            var picArr = this.answer.content_msg.pic_url
            for (let i = 0; i < picArr.length; i++) {
                var pic = {
                    url: picArr[i]
                }
                piclist.push(pic)
            }
            this.PicList = piclist
            this.isUpload = true
            this.isUploadok = true

        },
        notUpdate() {
            this.operaForm = {}
            this.isUpload = false
            this.isUploadok = false
        },
        onSubmit() {
            this.operaForm.title_to_publish_id = this.work._id

            if (this.mode == "上传文字") {
                this.operaForm.work_type = 1
            } else {
                if (this.mode == "上传文件") {
                    this.operaForm.work_type = 2
                } else {
                    if (this.mode == "上传图片") {
                        this.operaForm.work_type = 3
                    } else {
                        this.$message.error("请选择上传方式");
                        return
                    }
                }
            }

            if (this.fileList.length > 0) {
                this.operaForm.file_url = this.fileList[0].url
                this.operaForm.file_name = this.fileList[0].name
            }
            this.operaForm.pic_url = []
            for (let i = 0; i < this.PicList.length; i++) {
                var url = this.PicList[i].url;
                this.operaForm.pic_url.push(url)
            }
            work_upload(this.operaForm).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.$message({
                        message: '上传作业成功',
                        type: 'success',
                    });
                    this.getAnswerMessage();
                }
            })
        },
    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-top: 20px;
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;

        .el-form-item {
            margin-bottom: 0px;
        }
    }
}

.up {
    margin-top: 20px;
}

.cell-item {
    display: flex;
    align-items: center;
}
</style>
  